.missed-key{
    position: fixed;
    z-index: 100;
}

.keyboard-img{
    width: 100%;
    height: auto;
    position: relative;
    z-index: 2;
}

.keyboard-img svg path{
    fill: var(--selection-color) !important;
}

.key{
    position: absolute;
    height: 15%;
    width: 6%;
    z-index: 1;
}

.key1{
    position: absolute;
    height: 15%;
    width: 6%;
    z-index: 100;
}

.key1:hover{
    cursor: pointer;
}

